const app = getApp();
const common = require('../../../publicjs/common.js');
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
export default {
	components: {
		uniNavBar
	},
	data() {
		return {
		};
	},
	onLoad() {
		uni.stopPullDownRefresh(); //停止刷新
	},
	methods: {
		getUserInfo(res) {
			//获取用户信息
			let this_ = this;
			console.log(this)
			let iv = res.detail.iv;
			let encryptedData = res.detail.encryptedData;
			uni.getProvider({
				service: 'oauth',
				success(ProvRes) {
					console.log(ProvRes)
					if(~ProvRes.provider.indexOf("weixin")) {
						uni.login({
							provider: 'weixin',
							success:function(res){
								let code = res.code;
								common
									.requestURL({
										url: '/user/wxlogin',
										method: 'POST',
										params: JSON.stringify({ code, encryptedData, iv })
									})
									.then(res => {
										if (res.code == 200) {
											this_.$store.commit("userinfolist", res.data.user)
											this_.$store.commit("token", res.data.token);
											
											uni.redirectTo({
												url: "../index/index"
											})
											
											if(typeof res.data.college != "undefined"
												&& res.data.college != null
												&& res.data.college != "") {
													//已填写学院信息
													
												}else {
													//未填写学院信息的时候
													
													
												}
											
										}else {
											uni.showModal({
												title: '提示',
												content: '登录失败,请重试！',
											})
										}
									});
							},
							fail: res => {
								console.log('拒绝了');
								uni.showModal({
									title:"温馨提示",
									content: "请同意！",
									showCancel: false,
								})
							}
						});
					}else {
						uni.showModal({
							title:"温馨提示",
							content: "目前暂不支持，其他设备登录！",
							showCancel: false,
						})
					}
						
				}
			})
			
		}
	}
};
